.back{
    -webkit-clip-path: ellipse(16% 85% at 0% 14%);
    clip-path: ellipse(16% 85% at 0% 14%);
    width:100%;
    height:100vh;
    background-color:#14984d52;
    position: absolute;
    top:0;
}

.backOne{
    -webkit-clip-path: ellipse(25% 85% at 0% 14%);
    clip-path: ellipse(25% 85% at 0% 14%);
    width:100%;
    height:100vh;
    background-color:#1cad5b;
    position: absolute;
    top:0;
}

.signupHead{
    text-align: center;
    font-size: 40px;
    font-weight: bold;
}

.leftSignWelcome{
    font-size: 45px;
    margin-top: 8vh;
    margin-left:20px;
    color:black;
    font-weight: bold;
    text-align: left;
    color: white;
    text-shadow: 2px 2px 4px #000000;
}

.leftSign{
    font-size: 35px;
    margin-top: 8vh;
    margin-left:20px;
    color:black;
    font-weight: bold;
    text-align: left;
    color: white;
    text-shadow: 2px 2px 4px #000000;
}

.leftSignTwo{
    font-size: 30px;
    margin-top: 10vh;
    color:black;
    font-weight: 600;
    line-height: 1.6;
    text-align: left;
    margin-left: 20px;
    color: white;
    text-shadow: 2px 2px 4px #000000;
}

.warpForm{
    background-color: #00ba51;
    border-radius: 10px;
    width: 70%;
    padding: 30px;
    margin: auto;
    text-align: left;
    color:#fff;
    margin-bottom: 10px;
}

.warpBtn{
    display: flex;
    justify-content: space-between;
}

.textRed{
    color: rgb(199, 33, 33);
    font-size: 14px
}

.btnLight{
    color:#fff;
    border: 2px solid #fff;
    background-color: #1cad5b00;
    font-weight: bold;
}

.btnLight:hover{
    color:#00ba51;
    border: 2px solid #fff;
    background-color: #fff;
}

